@import "../../dev/variables.sass";
@import "../../dev/mixins.sass";

footer#footer {
    background-color: $footer-bg;
    height: $footer-height;
    line-height: $footer-height;
    position: relative;
    margin-top: -$footer-height;
    z-index: 99999;
    > .container {
        position: relative; }
    .brand {
      position: absolute;
      left: $container-padding;
      bottom: 100%;
      height: 150px;
      margin-bottom: -$footer-height;
      z-index: 9;
      + .brand {
        margin-left: 120px; } }
    ul.social {
      float: right; } }
@include responsive(mobile) {
  footer#footer {
    position: relative;
    > .container {
      position: static; }
    .brand {
      left: 10px; } } }
