@import "../dev/variables.sass";
@import "../dev/mixins.sass";

.btn {
  @include transition(background-color .3s ease-in-out);
  border-radius: 30px;
  display: inline-block;
  padding: 15px 45px;
  font-family: $font-family;
  font-size: 16px;
  font-weight: $extra-bold;
  border: none;
  min-width: 220px;
  box-sizing: border-box;
  cursor: pointer;
  &:hover {
    text-decoration: none; }
  &:focus {
    outline: none; }
  &.space {
    margin: 60px auto; }
  &.block {
    border-radius: 0;
    width: 100%;
    font-weight: $light; }
  &.red {
    background-color: $light-red;
    color: #fff;
    &:hover {
      background-color: #fff;
      color: $light-red; } }
  &.primary {
    background-color: $red;
    color: #fff;
    &:hover {
      background-color: #fff;
      color: $red; } }
  &.white {
    color: $red;
    background-color: #fff; } }

label {
  display: block;
  margin: 20px 0;
  font-weight: $regular;
  font-style: italic;
  font-family: $font-family;
  font-size: 16px; }

input,
textarea {
  @include transition(all .4s ease-in-out);
  border: none;
  padding: 20px;
  background-color: $border-color;
  width: 100%;
  box-sizing: border-box;
  margin-bottom: 10px;
  font-size: 16px;
  font-family: $font-family;
  &.material {
    border-bottom: 1px solid $border-color;
    padding-bottom: 10px;
    padding-left: 0;
    padding-right: 0;
    padding-top: 0;
    background-color: transparent; }
  &:focus {
    outline: none;
    border-bottom-color: $primmary-color; } }

textarea {
  height: 150px;
  resize: vertical; }

.file {
  position: relative;
  overflow: hidden;
  input[type='file'] {
    opacity: 0;
    position: absolute;
    top: -200%; }
  .btn {
    width: 100%;
    position: relative;
    overflow: hidden;
    z-index: 1;
    &.success {
      span {
        @include scale(1);
        background-color: #00bf1e; } }
    &.warning {
      span {
        @include scale(1);
        background-color: #900210; } }
    span {
      @include transition(transform .3s ease-in-out);
      position: absolute;
      left: 0;
      top: 0;
      z-index: -1;
      width: 100%;
      height: 100%;
      @include scale(0);
      text-align: center;
      font-size: 12px;
      font-weight: $light; } } }

.check {
  position: relative;
  overflow: hidden;
  input[type='checkbox'] {
    opacity: 0;
    position: absolute;
    top: -200%;
    &:checked {
      &+label {
        &::before {
          background-image: url('../../images/icon/tick.png');
          background-position: center;
          background-size: cover; } } } }
  label {
    position: relative;
    display: inline-block;
    font-style: normal;
    font-weight: $regular;
    &::before {
      content: '';
      float: left;
      width: 21px;
      height: 21px;
      color: #fff;
      background-color: $primmary-color;
      margin-right: 18px; }
    a {
      color: $primmary-color;
      text-decoration: underline; } } }
