@import 'dev/variables';
@import 'dev/mixins';
@import '../../lib/flexiblegs-sass/flexiblegs-sass.sass';

@import '../../fonts/font.css';
//@import '../icon-fonts/style.css'

@import 'partial/header/header';
@import 'partial/footer/footer';

@import 'components/form';

* {
  margin: 0;
  padding: 0; }

html,
body,
#wrapper {
  height: 100%; }

#wrapper {
  height: auto;
  min-height: 100%; }

#page-content {
  padding-bottom: $footer-height; }

body {
  background-color: $bg-color;
  font-family: $font-family; }

.text-center {
  text-align: center; }

.text-left {
  text-align: left; }

.text-right {
  text-align: right; }

.color-red {
  color: $red; }

.color-purple {
  color: $purple !important;
  &:hover {
    color: darken($purple, 5%) !important; } }

.color-pink {
  color: $pink !important;
  &:hover {
    color: darken($pink, 5%) !important; } }

.color-white {
  color: #fff; }

.container {
  max-width: $lg-width;
  margin: 0 auto;
  padding: 0 $container-padding;
  box-sizing: border-box;
  &.no-pad {
    padding: 0; }
  &.right {
    margin-left: auto;
    margin-right: 0; }
  &.left {
    margin-right: auto;
    margin-left: 0; }
  &.clear {
    &::after {
      @include clearfix;
      content: ''; } } }
ul {
  list-style: none; }

a {
  text-decoration: none;
  &:focus {
    outline: none; } }

section {
  > header {
    margin-bottom: 85px;
    .title {
      font-weight: $extra-bold;
      font-style: italic;
      font-size: 22px; }
    .note {
      margin-top: 65px;
      font-weight: $light;
      font-style: italic;
      font-size: 24px; } }
  > footer {
    margin-top: 28px;
    p {
      max-width: 74%;
      margin: 0 auto;
      font-weight: $light;
      font-style: italic;
      font-size: 16px; } }
  &.space {
    margin: 60px 0; } }

.skewYTop {
  position: relative;
  padding: 300px 0px 100px 0px;
  z-index: 2;
  background-color: rgba($primmary-color, .8);
  .skew-bg,
  &::after {
    position: absolute;
    width: 100%;
    box-sizing: border-box;
    left: 0;
    z-index: -1; }
  .skew-bg {
    background-size: cover;
    background-position: center -250px;
    height: 100%;
    top: 0;
    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: $skew-section-bg; } }
  &::after {
    content: '';
    height: 250px;
    top: -125px;
    background-color: $bg-color;
    @include skewY(-7deg);
    -ms-filter: 'progid:DXImageTransform.Microsoft.Matrix(M11=1, M12=0, M21=-0.12278456090290474, M22=1, SizingMethod='auto expand')';
    filter: progid:DXImageTransform.Microsoft.Matrix(M11=1,M12=0,M21=-0.12278456090290474,M22=1,SizingMethod='auto expand'); } }
.skewY {
  position: relative;
  padding: 200px 0px;
  z-index: 2;
  &.space {
    margin: 160px 0; }
  .skew-bg,
  &::after {
    position: absolute;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    left: 0;
    top: 0;
    @include skewY(7deg);
    -ms-filter: 'progid:DXImageTransform.Microsoft.Matrix(M11=1, M12=0, M21=0.12278456090290524, M22=1, SizingMethod='auto expand')';
    filter: progid:DXImageTransform.Microsoft.Matrix(M11=1,M12=0,M21=0.12278456090290524,M22=1,SizingMethod='auto expand');
    z-index: -1; }
  .skew-bg {
    background-position: center; }
  &::after {
    content: '';
    background-color: $skew-section-bg; } }
ul.social {
  &:after {
    content: '';
    @include clearfix; }
  li {
    float: left;
    a {
      display: block;
      img {
        display: inline-block;
        vertical-align: middle; } }
    &+li {
      margin-left: 10px; } } }
#page-space {
  padding: 210px 0 260px 0; }
.title2 {
  font-style: italic; }
.row {
  @include wrap;
  @include wrap(3);
  @include wrap('gutter', 15);
  .text-input {
    @include col; } }
.row4 {
  @include wrap;
  @include wrap(4);
  @include wrap('gutter', 25);
  .col {
    @include col; }
  .file {
    @include col; } }
@include responsive(mobile) {
  .container {
    padding: 0 $container-padding/2; }
  section {
    > header {
      margin-bottom: 65px;
      .note {
        margin-top: 45px; } }
    > footer {
      margin-top: 8px;
      p {
        margin: 0 auto; } }
    &.space {
      margin: 40px 0; } }
  .skewY {
    padding: 60px 0;
    &.space {
      margin: 140px 0; } }
  .skewYTop {
    padding: 200px 0px 80px 0px;
    margin-top: -125px; }
  #page-space {
    padding: 150px 0 100px 0; }
  .row {
    @include wrap(1); }
  .row4 {
    @include wrap(1); } }
