@import "../../dev/variables.sass";
@import "../../dev/mixins.sass";
header#header {
  padding: $header-padding 0;
  #navbar {
    background-color: $navbar-bg;
    height: $navbar-height;
    > .container {
      position: relative;
      &::after {
        @include clearfix();
        content: ''; } }
    .brand {
      position: absolute;
      left: $container-padding;
      top: -$header-padding;
      @include box-shadow(0px -10px 10px rgba(0, 0, 0, 0.62)); }
    nav#nav {
      float: right;
      ul {
        &::after {
          @include clearfix();
          content: ''; }
        li {
          float: left;
          margin-right: 20px;
          line-height: $navbar-height;
          a {
            display: block;
            font-size: $nav-font-size;
            font-weight: $light;
            color: $nav-font-color; }
          &.icon-menu {
            a {
              font-size: 15px;
              color: $primmary-color;
              display: inline-block;
              padding: 0 15px 0 5px;
              border-radius: 29px;
              height: 29px;
              line-height: 29px;
              background-color: #fff;
              vertical-align: middle;
              img, span {
                display: inline-block;
                vertical-align: middle; }
              img {
                margin-right: 8px;
                margin-top: -2px; } } } } } } } }
@include responsive(mobile) {
  header#header {
    #navbar {
      position: relative;
      height: auto;
      &.open {
        nav#nav {
          display: block;
          ul {
            li {
              &:hover {
                &::before {
                  width: 100%; }
                a {
                  color: #fff; } } } } }
        #mobile-menu-btn {
          span {
            &:first-child {
              @include rotate(45deg); }
            &:last-child {
              @include rotate(-45deg);
              top: -7.5px; } } } }
      > .container {
        position: static; }
      .brand {
        left: 10px;
        z-index: 10; }
      #mobile-menu-btn {
        display: inline-block;
        vertical-align: middle;
        float: right;
        padding: $navbar-height/2 0;
        span {
          display: block;
          width: 20px;
          height: 2px;
          background-color: #fff;
          position: relative;
          &+span {
            margin-top: 5px; } } }
      nav#nav {
        position: absolute;
        z-index: 9;
        left: 0;
        top: 100%;
        margin-top: 20px;
        background-color: transparent;
        width: 100%;
        display: none;
        ul {
          li {
            float: none;
            background-color: #fff;
            padding: 0 $navbar-height/2;
            width: 100%;
            box-sizing: border-box;
            text-align: right;
            position: relative;
            z-index: 1;
            &+li {
              margin-top: 1px; }
            &::before {
              content: '';
              position: absolute;
              left: 0;
              top: 0;
              width: 0;
              height: 100%;
              z-index: -1;
              background-color: $primmary-color;
              @include transition(width .5s ease-in-out); }
            a {
              color: $primmary-color; } } } } } } }
